<template>
  <transition name="fade">
    <div v-if="visible" class="fly-loading" :class="{'fly-loading--visible': visible}">
      <div class="fly-loading__inner">
        <i></i>
        &nbsp;
        <span>{{text || 'Loading...'}}</span>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "loading",
  props: {
    text: {
      type: String,
    },
    visible: {
      type: Boolean,
    },
  },
}
</script>

<style lang="less" scoped>
@keyframes rotate-animation {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fly-loading {
  display: none;
  position: fixed;
  z-index: 99999;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.1);
  &.fly-loading--visible {
    display: block;
  }
  &__inner {
    position: absolute;
    width: 150px;
    line-height: 30px;
    color: white;
    padding: 15px;
    left: 50%;
    margin-left: -90px;
    top: 50%;
    margin-top: -10px;
    background: rgba(19, 33, 73, 0.878);
    box-shadow: 0 0 24px 0 rgba(30, 85, 168, 0.31);
    border-radius: 4px;
    i {
      display: inline-block;
      width: 24px;
      height: 24px;
      vertical-align: middle;
      animation: rotate-animation 1s linear infinite;
      background: url(data:image/png;base64,);
    }
    span {
      vertical-align: middle;
    }
  }
}
</style>



